				"html": "<h1>Java Control Flow</h1>\n<p>Control flow statements are exactly what the term means. They are statements that alter execution flow based on decisions, loops and branching so that the program can conditionally execute blocks of code.</p>\n<p>Primarily, Java has the following constructs for flow control:</p>\n<ul>\n<li>\n<p><code>if</code></p>\n<pre><code>    if( &#x3C;expression that results in a boolean>) {\n     //code enters this block if the above expression is 'true'\n    }\n</code></pre>\n</li>\n<li>\n<p><code>if...else</code></p>\n<pre><code>    if( &#x3C;expression that results in a boolean> ){\n      //execute this block if the expression is 'true'\n\n    } else{\n      //execute this block if the expression is 'false'\n    }\n</code></pre>\n</li>\n<li>\n<p><code>switch</code></p>\n</li>\n</ul>\n<p>Switch is an alternative for the <code>if...else</code> construct when there are multiple values and cases to check against.</p>\n<pre><code>        switch( &#x3C;integer / String / Enum > ){\n            case &#x3C;int/String/Enum> : &#x3C;statements>\n            break;\n            case &#x3C;int/String/Enum> : &#x3C;statements>\n            default: &#x3C;statements>\n        }\n</code></pre>\n<p>Note: The program flow <code>falls through</code> the next <code>case</code> if the <code>break</code> statement is missing. For e.g. Let's say you say the standard 'Hello' to everyone at office, but you are extra nice to the girl who sits next to you and sound grumpy to your boss. The way to represent would be something like:</p>\n<pre><code>    switch(person){\n           case 'boss' : soundGrumpy();\n           break;\n           case 'neighbour' : soundExtraNice();\n           case 'colleague': soundNormal();\n           break;\n           default :\n           soundNormal();\n       }\n\nNote: The `default` case runs when none of the `case` matches. Remember that when a case has no `break` statement, it `falls through` to the next case and will continue to the subsequent `cases` till a `break` is encountered.\n</code></pre>\n<ul>\n<li><code>nested statements</code></li>\n</ul>\n<p>Any of the previous control flows can be nested. Which means you can have nested <code>if</code>,<code>if..else</code>and<code>switch..case</code>statements. i.e., you can have any combination of these statements within the other and there is no limitation to the depth of<code>nesting</code>.</p>\n<p>For example, let's consider the following scenario:</p>\n<p>If you have less than 25 bucks, you get yourself a cup of coffee. If you have more than 25 bucks but less than 60 bucks, you get yourself a decent meal. If you have more than 60 bucks but less than a 100, you get yourself a decent meal along with a glass of wine. However, when you have more than a 100 bucks, depending on who you are with, you either go for a candle lit dinner (with your wife) or you go to a sports bar (with your friends).</p>\n<p>One of the ways to represent this will be:</p>\n<pre><code>int cash = 50;\nString company = \"friends\";\n\nif(cash&#x3C;25){\n    getCoffee();\n}else if(cash&#x3C;60){\n    getDecentMeal();\n}else if(cash&#x3C;100){\n    getDecentMeal();\n    getGlassOfWine();\n}    else {\n\n    switch(company){\n        case \"wife\" : candleLitDinner();\n        break;\n        case \"friends\" : meetFriendsAtSportsBar();\n        break;\n    }\n\n}\n</code></pre>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=2\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://repl.it/CJZi/1\">Run Code</a></p>",
